'use client'

import { Card } from 'frosted-ui'
import { useWhop } from '~/components/whop-context'

export default function DashboardPage() {
  const { access } = useWhop()
  const isAdmin = access.accessLevel === 'owner' || access.accessLevel === 'admin'
  if (!isAdmin) {
    return (
      <div className="p-6">
        <div className="text-sm opacity-70">You don't have access to this page.</div>
      </div>
    )
  }
  return (
    <div className="p-6 grid gap-4 grid-cols-1 md:grid-cols-2">
      <Card><div className="p-4">Overview</div></Card>
      <Card><div className="p-4">Recent activity</div></Card>
    </div>
  )
}


